@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600&display=swap);
/*Import Poppins from Google Fonts*/

body {
  margin: 0;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*App CSS*/
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: Poppins;
  min-height: 100vh;
}

.App-logo {
  height: 75px;
}

.App-title{
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-weight: bold;
}
.App-text{
  font-size: 1.25rem;
  font-weight: 500;
}

.App-secondary-button{
  border-radius: 100rem;
  margin: 3rem 0rem;
  border: 2px solid white;
  background-color: transparent;
  font-size: 1.25rem;
  padding: 1rem 2rem;
  font-weight: bold;
  color: white;
  font-family: Poppins;
}
.App-secondary-button:hover{
  border-radius: 100rem;
  margin: 3rem 0rem;
  border: 2px solid  rgba(255,255,255,0.55);;
  background-color: transparent;
  font-size: 1.25rem;
  padding: 1rem 2rem;
  font-weight: bold;
  color: rgba(255,255,255,0.55);
  font-family: Poppins;
  cursor:pointer;
}
.App-link {
  color: #61dafb;
}

#Animations
.fade-enter{
  opacity:0.01;
}
.fade-enter{
  opacity:0.01;
}
.fade-enter.fade-enter-active{
  opacity:1;
  transition: opacity 350ms ease-in;
}
.fade-exit{
  opacity:1;
}
.fade-exit.fade-exit-active{
  opacity:0.01;
  transition: opacity 350ms ease-in;
}
.Home-container {
    padding:1.1rem
}
.Card-body{
    padding: 2rem 2rem;
    background-color: white;
    border-radius: 10px;
    height: 100%;
    max-height: 500px;
    box-shadow: 0px 15px 20px rgba(150, 50, 85, 0.1);
    margin-bottom: 100px;
    transition: transform .3s ease-out;
}

.Card-title{
    color: #BE5A78;
    font-weight: 800;
    font-size: 1.5rem;
}

.Card-img{
    width: 185px;
    padding: 1rem;
}

  .Card-body:hover {
    transform: translate(0, -5px);
    cursor: pointer;
    box-shadow: 0px 15px 20px rgba(150, 50, 85, 0.05);
  }
.Tradition-overlay{
    background-color: rgba(255, 255, 255, 0.85);
    width: 100%;
    height: 100vh;
}

.Tradition-icon{
    height: 50px;
    margin-top: 3rem;
    margin-bottom: 2rem;
}

.Tradition-quote{
    font-weight: bold;
    font-size: 2rem;
    line-height: 3.5rem;
    margin-bottom: 2rem;
    color: #B44367;
}

.Tradition-source{
    font-weight: 600;
    color: #9e2d52;
    font-size: 1rem;
}

.Tradition-button{
    border-radius: 100rem;
    margin: 3rem 1rem;
    border: 3px solid #1e1cb9;
    background-color: #1e1cb9;
    font-size: 1.2rem;
    padding: 1rem 2rem;
    font-weight: 600;
    color: white;
}

.Tradition-repeat-button{
    border-radius: 100rem;
    margin: 3rem 1rem;
    border: 3px solid #9e2d52;
    background-color: transparent;
    font-size: 1.2rem;
    padding: 1rem 2rem;
    font-weight: 600;
    color: #9e2d52;
}
